import { computed, inject, ref } from "vue";
import { useStore } from "vuex";

export function useCryoStorageList() {
    const store = useStore();
    const storageList = computed(() => store.state.cryoStorage.list);
    const loading = ref(false);

    const getStorageList = () => {
        loading.value = true;
        store
            .dispatch(
                "cryoStorage/getCyroStorageByPatient",
                store.state.artCycle.selectedCycle.patientId
            )
            .finally(() => {
                loading.value = false;
            });
    };

    return { storageList, getStorageList, loading };
}

export function useCryoStorageReceivedEmbryos() {
    const store = useStore();
    const dialogVisible = ref(false);
    const receivedEmbryos = computed(
        () => store.state.cryoStorage.receivedEmbryos
    );
    const loading = ref(false);

    const getReceivedEmbryos = () => {
        loading.value = true;
        store
            .dispatch(
                "cryoStorage/getCryoStorageList",
                store.state.artCycle.selectedCycle.cycleId
            )
            .finally(() => {
                loading.value = false;
            });
    };

    return { dialogVisible, receivedEmbryos, getReceivedEmbryos, loading };
}

export function useReceivedEgss() {
    const store = useStore();
    const fertilizationLines = computed(
        () => store.state.cryoStorage.fertilizationLines
    );
    const loading = ref(false);

    const getFertilizationLines = () => {
        loading.value = true;
        store
            .dispatch(
                "cryoStorage/getFertilizationLineByCycle",
                store.state.artCycle.selectedCycle.cycleId
            )
            .finally(() => {
                loading.value = false;
            });
    };

    return { fertilizationLines, getFertilizationLines  , loading };
}

export function useReceivedEmbryoAddDialog() {
    const dialogVisible = inject("dialogVisible");

    return { dialogVisible };
}
